@font-face {
    font-family: "Exo 2.0";
    src: url("../../fonts/Exo2.0-Thin.otf");
}

@media (orientation: portrait) {
    .cardsContainer {
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 63%;
        flex-wrap: wrap;
    }
}

@media screen and (max-height: 681px) {
    .cards {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        background-image: url("../../img/Background@2x.png");
        background-size: auto;
    }
}

.cards {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    background-image: url("../../img/Background.png");
    background-size: auto;
}

.cardsContainer {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    flex-wrap: wrap;
}

.containerHeader {
    font-size: 36px;
    font-family: "Exo 2.0", sans-serif;
    color: #FFFFFF;
    margin-bottom: 20px;
}